.box-pagination {
    display: inline-block;
}
.pagination {
    display: flex;
    padding-left: 0;
    list-style: none;
    margin-bottom: 20px;
}
.pagination li {
    padding: 2px;
}
.pagination li a {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    color: $color-brand-1;
    border-radius: 4px;
    border: 1px solid $color-gray-80;
    min-width: 36px;
    text-align: center;
}
.pagination li .page-prev {
    background: url(../imgs/page/shop/prev.svg) no-repeat center;
    min-height: 37px;
    padding: 5px 20px;
    border: 0px;
}
.pagination li .page-next {
    background: url(../imgs/page/shop/next.svg) no-repeat center;
    min-height: 37px;
    padding: 5px 20px;
    border: 0px;
}
.pagination li a:hover, .pagination li a.active {
    background-color: $color-brand-1;
    border-color: $color-brand-1;
    color: $color-gray-80;
}
.pagination li .page-next:hover {
    background-color: transparent;
}
.pagination li .page-prev:hover {
    background-color: transparent;
}
.rating img {
    width: 12px;
}
.box-count
{
    text-align: left;
    margin: 0px 0px 20px 0px;
    .deals-countdown
    {
        border-radius: 0px;
        display: flex;
        padding: 4px 0px;
        width: 100%;
        margin: auto;
        max-width: 100%;
        .countdown-section
        {
            display: inline-block;
            text-align: center;
            width: 25%;
            line-height: 12px;
            position: relative;
            span
            {
                display: block;
                color: $color-white;
            }
            .countdown-period
            {
                text-transform: capitalize;
            }
            &:last-child
            {
                &::before
                {
                    display: none;
                }
            }
        }
    }
    &.box-count-square
    {
        margin-bottom: 5px;
        .deals-countdown
        {
            background-color: $background-white;
            width: 100%;
            margin: auto;
            text-align: left;
            display: flex;
            max-width: 100%;
            .countdown-section
            {
                display: inline-block;
                text-align: center;
                line-height: 12px;
                position: relative;
                margin: 0px 16px 0px 0px;
                width: auto;
                .countdown-period
                {
                    color: $color-gray-500;
                    font-size: 12px !important;
                    line-height: 24px !important;
                    font-weight: bold;
                    text-transform: uppercase;
                }
            }
            .countdown-amount
            {
                background-color: $color-gray-60;
                border: 1px solid $color-gray-80;
                border-radius: 4px;
                color: $color-brand-1;
                padding: 10px 0px;
                width: 78px;
                height: 80px;
                display: inline-block;
                margin-bottom: 5px;
                font-size: 32px !important;
                line-height: 55px !important;
            }
        }
    }
}